@import url(https://fonts.googleapis.com/css?family=Orbitron:700);
.Clock {
  margin: 0 auto;
  width: 300px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
}

.Ticker h1 {
  position: relative;
  top: 210px;
  left: 90px;
  z-index: 1;
  color: white;
  font: 700 1.7em 'Orbitron'; 
  text-shadow: 1px 1px black;
}

.Controls {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.brrng {
  -webkit-animation: 0.6s shake forwards;
          animation: 0.6s shake forwards 
}

@-webkit-keyframes shake {
    0% {-webkit-transform:     translateX(2px);transform:     translateX(2px)}
    10% {-webkit-transform:    translateX(-2px);transform:    translateX(-2px)}
    20% {-webkit-transform:    translateX(7px);transform:    translateX(7px)}
    30% {-webkit-transform:    translateX(-7px);transform:    translateX(-7px)}
    40% {-webkit-transform:    translateX(10px);transform:    translateX(10px)}
    50% {-webkit-transform:    translateX(-10px);transform:    translateX(-10px)}
    60% {-webkit-transform:    translateX(20px);transform:    translateX(20px)}
    70% {-webkit-transform:    translateX(-20px);transform:    translateX(-20px)}
    80% {-webkit-transform:    translateX(10px);transform:    translateX(10px)}
    90% {-webkit-transform:    translateX(-10px);transform:    translateX(-10px)}
    100% {-webkit-transform:   translateX(0) translateY(0);transform:   translateX(0) translateY(0)}
}

@keyframes shake {
    0% {-webkit-transform:     translateX(2px);transform:     translateX(2px)}
    10% {-webkit-transform:    translateX(-2px);transform:    translateX(-2px)}
    20% {-webkit-transform:    translateX(7px);transform:    translateX(7px)}
    30% {-webkit-transform:    translateX(-7px);transform:    translateX(-7px)}
    40% {-webkit-transform:    translateX(10px);transform:    translateX(10px)}
    50% {-webkit-transform:    translateX(-10px);transform:    translateX(-10px)}
    60% {-webkit-transform:    translateX(20px);transform:    translateX(20px)}
    70% {-webkit-transform:    translateX(-20px);transform:    translateX(-20px)}
    80% {-webkit-transform:    translateX(10px);transform:    translateX(10px)}
    90% {-webkit-transform:    translateX(-10px);transform:    translateX(-10px)}
    100% {-webkit-transform:   translateX(0) translateY(0);transform:   translateX(0) translateY(0)}
}

button {
  background: none;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font: 700 1.2em 'Orbitron';
  color: tomato;
}


